<template>
    <h1 id="Set_Examples">Set Examples</h1>
    <p>Here are some basic explanations of both an offensive and defensive set to show just how easy it is to create basic Loomian PVP Sets:</p>
    <div class="box">
        <div class="selection">
            <button v-for="(Rally, Index) in Items" 
            :key="Index" 
            @click="SwitchTabs(Rally.Name)">
            {{ Rally.Name }}
        </button>
        </div>
        <div class="description"
        :style="{ display: Rally.Name === Items[0].Name ? 'block' : 'none' }"
        v-for="(Rally, Index) in Items" 
        :key="Index" 
        :id="Rally.Name">
            <h2>{{ Rally.Name }}</h2>
            <template v-for="(Text, Index) in Rally.Description" :key="Index">
                <template v-if="isImagePath(Text)">
                    <img class="image" :src="Text">
                    <br>
                </template>
                <template v-else-if="Text == '<br>'">
                    <br>
                </template>
                <template v-else>
                    <p>{{ Text }}</p>
                </template>
            </template>   
        </div>
    </div>
</template>
<script>
    export default {
        name: 'SetExamples',
        data() {
            return {
                Items: [
                    {
                    Name: "Offensive Set - Example 1", 
                    Description: [                        
                        `Among all the Beginner Loomians, Zuelong stands as one of the most popular. It always has had a strong presence as a revenge killer or late game cleaner. Despite having answers coming and going, it still sees sufficient usage and viability nonetheless.`,
                        `One of the main reasons Zuelong has such great offensive utility is because of its base stats being sufficient to do so. Zuelong features an impressive base speed stat of 119 making it one of the fastest Loomians without the need of a boost. Furthermore, it has a satisfactory Ranged Attack stat at 106. Its energy stat sits at 88 allowing it to reach a reasonable benchmark so it can fire off enough moves. Despite Zuelong being known for being a ranged attacker, it does have a somewhat usable Melee Attack stat of 89 that can catch common Zuelong answers off guard however for this demonstration it wont be focused on.`,
                        require("@/assets/images/setMaking/OffensiveSet3/BaseStats.png"),
                        `Despite it being an offensive loomian, with less than decent defensive stats, It does have a major resistance to electric and 4 resistances. On the other hand, it does have 3 weaknesses being light, earth and ancient.`,
                        `However, it is the offensive effectiveness that most people are interested in. Taking in account its relevant ranged attacks, it can hit a broad selection of Loomians for at least neutral effectiveness.`,
                        require("@/assets/images/setMaking/OffensiveSet3/TypeEffectiveness.png"),
                        `Despite having a great base speed stat, the meta is only becoming faster with it seeing Loomians faster than ever with boots, moves and abilities. For this reason, its recommended to run Nimble, enabling it to outspeed offensive threats like Hunder, Jetwing Stratusoar and Chienta.`,
                        `Additionally, Zuelong appreciates having some damage increase given its output would be lacklustre without it. For this reason, it can run Smart too. As for its negative personality, Very Frail is more than enough given it wont run melee moves.`,
                        require("@/assets/images/setMaking/OffensiveSet3/Ability.png"),
                        `Awakening is a great ability boosting Zuelongs type-sharing moves by an additional 25% however Brute Force is marginally better, granted it removes secondary effects such as a potential burn from Elemental Burst. Despite this, being able to knock out opponents is better than the probability of inflicting an ailment.`,
                        `For a considerable amount of time, Zuelong didnt necessarily have a full moveset to run, but it now has quite a few options. The main three moves it should have is Thunderstrike, Elemental Burst and Fire Breath however its last move can be Ancient Roar - to pivot on Loomians it doesnt like staying on whilst decreasing their offensive presence; Zip-Zap - acting as an alternative pivot move that deals marginal damage and Jolt enabling Zuelong to jump priority to finish off Loomians that may be faster otherwise. `,
                        require("@/assets/images/setMaking/OffensiveSet3/Moveset.png"),
                        `Zuelong sports a simple TP spread: 200 points in both its Speed and RAtk for maximum speed and damage output, respectively; 20 points in Energy, allowing it to reach 250 and use any of its three main attacks five times; and 80 points in Health as leftovers.`,
                        require("@/assets/images/setMaking/OffensiveSet3/TPs.png"),
                        `Usually an Essence of your preference can be used on Zuelong however some players may opt for Specialty Boots so it can comfortably act as a revenge killer without another Boots Loomian outspeeding it. Alternatively, if speed control isn't a necessity, Specialty Goggles allows Zuelong to hit at the equivalent of +1 - a boost in attack that it hasn't gotten before. Both specialty items do come with the disadvantage of being locked into the first move chosen so choose wisely.`,                        
                    ]},
                    {
                    Name: "Offensive Set - Example 2", 
                    Description: [
                        `At first glance, Hunders stats dont seem special apart from its great speed of 116 which still holds potential in the current meta. Its Melee Attack sits at 94 while its Ranged attack is 43. Both of its defences are not exactly abundant with its Melee Defence and Ranged Defence being 83 and 69 respectively. This isnt a huge hindrance considering Hunder is used offensively (and has the speed to avoid being outsped). Hunders energy is 84 which allows it to feasibly reach the 250 benchmark. Finally its Health is 78 which is quite middle in the pack however this isnt a big matter.`,
                        require("@/assets/images/setMaking/OffensiveSet2/BaseStats.png"),
                        `Hunder is a Metal (like most Sepharite City Loomians) and Electric type which means it has a quad weakness to Earth and a normal weakness to Fire, Water and Brawler. However, quite unexpectedly, it does possess some resistances such as Ice, Bug, Ancient and Mind. It also has 2 major resistances being Metal and Air.`,
                        require("@/assets/images/setMaking/OffensiveSet2/TypeEffectiveness.png"),
                        `Offensively, on the contrary, taking into account its relevant moves, Hunder can hit nearly every Loomian for at least neutral save for a couple of fire types.`,
                        `Given Hunder is a Melee Attacker, it makes sense to use the positive personality Brawny. However, despite its already great speed stat, it does sit between other Loomians who would want to outspeed Hunder who either have the same speed stat or are only slightly below. For this reason, its highly recommended to run Nimble`,
                        `As for the negative ability, Very Clumsy will be used due to its uselessness.`,
                        `Hunders Melee Attack is decent for the most part however it can be improved so an ability that increases damage output would be greatly helpful. Brute Force does increase damage output by 30% at the cost of secondary effects but Power Jaw takes this a step further by increasing damage by 50% for all biting moves. For this reason, and also because a lot of Hunders best moves are biting-based, we will use Power Jaw.`,
                        require("@/assets/images/setMaking/OffensiveSet2/PowerJaw.png"),
                        `As mentioned previously, Hunder has a lot of biting moves such as Blaze Chomp, Vice Jaws, Thunder Chomp etc. The specific combination well choose is Vice Jaws, Thunder Chomp, Chilly Chomp and Blaze Chomp.`,
                        `Vice Jaws prevents the target from using the same move for more than two moves and acts as one of Hunders ATB moves. Thunder Chomp enables Hunder to hit water types as well as other metal types. Chilly Chomp is extremely helpful against Earth types as Hunder is 4x weak to them. Finally, Blaze Chomp helps hit dark types, metal types who have an electric immunity and generally any Loomian that doesnt get hit by the other 3 moves.`,
                        require("@/assets/images/setMaking/OffensiveSet2/Moveset.png"),
                        `Hunder follows the simple 200 200 100 pattern by having 200 Melee Attack, 200 Speed, 52 Energy and 48 Health. `,
                        `200 Melee Attack for maximum damage output, 200 Speed speedties Nimble Dorogo, outspeeds Very Nimble Garlash, 52 Energy gives 5 Vice Jaws and 48 Health acts as leftovers`,
                        require("@/assets/images/setMaking/OffensiveSet2/TrainingPoints.png"),
                        `Hunder is all about powerful damage - and it also appreciates its diverse moveset. Thats why Hunder will be running Power Cuffs. The amount of TPs invested into Energy that was described earlier takes this into account so Hunder can still pull off quite a few moves before needing to regain energy.`,
                    ]},
                    {
                    Name: "Defensive Set - Example 1", 
                    Description: [
                        `Introduced with Lotusun Beach in Patch 7, Barblast is one of the evolutions for Pwuff. It became an instant favourite given the utility it offered and therefore high viability, especially over its Melee-Attacking Counterpart.`,
                        `Barblasts defensive nature in the meta is quite obvious when you notice its 96 Health, 102 Melee Defense and 93 Ranged Defence. Though its Energy and Ranged Attack - 83 and 89 respectively - is less than preferred, theyre still usable. Barblasts speed is at a depressing 37 but this is only expected given the maximisation of bulk. `,
                        require("@/assets/images/setMaking/DefensiveSet1/BaseStats.png"),
                        `Barblast has a respectable 3 weaknesses (being Electric, Earth and Mind) and 5 resistances (Fire, Water, Ice, Metal and Brawler). Melee Attackers such as Kanibo, Vesperatu, Garlash and even Obsidrugon struggle against Barblast given its typing. There are some Loomians that Barblast can act as a hard check against given the scenario however these will be mentioned later on.`,
                        require("@/assets/images/setMaking/DefensiveSet1/TypeEffectiveness.png"),
                        `Additionally, since Barblast is a toxic type, it has the ability to clear poison barbs from your side of the field.`,
                        `Given Barblast excels in Melee Defense and Health, Very Robust Frail Sluggish will be used. This increases Melee Defense by 20% whilst decreasing its Melee Attack and Speed respectively by 10%. Given these two stats are below mediocre, we can afford to decrease them further.`,
                        `Alternatively, we could utilize Clever in order to check Cephalops, Ikazune and many other ranged attackers that Barblast can hit for super effectiveness. However, for this demonstration, well be sticking with Very Robust.`,
                        `To increase the damage output from its 89 Ranged Attack, High Explosive boosts Barblasts explosive moves (such as Toxiblast and Water Bomb) by 50%. There is a downside to this ability where secondary effects are removed - similar to brute force - but this isnt a massive downside.`, 
                        require("@/assets/images/setMaking/DefensiveSet1/HighlyExplosive.png"),
                        `Toxic Spines, an ability that gives the chance for an attacker to be poisoned when using a contact move on Barblast, can also be used however some may find the odds of this ability not worth it. Well go ahead with the Secret Ability. `,
                        `Barblast isnt short of moves when it comes to its movepool. Since we are using High Explosive, Toxiblast and Water Bomb are the most obvious choices for ATB. Because This is a defensive loomian, recovery is a must. Rejuvenate will be chosen because of the high amount of health it can return. Finally, for the fourth move, various utility moves can be used but - disappointingly - we cant choose them all. Poison helps cripple and break down Loomians who stand in Barblasts way while Icicle Traps help exert pressure due to limiting swap-ins. Power Focus allows Barblast to set up allowing it to hit harder and make it less likely to hit weakly. This set will use Power Focus however any of the three are valid`,
                        require("@/assets/images/setMaking/DefensiveSet1/Moveset.png"),
                        `200 Health and 200 Melee Defense naturally seems the way to go with Barblast given how Melee Bulky we want it to be, allowing it to soak up as much damage as possible from previously mentioned melee attackers.`,
                        `Despite a somewhat suboptimal energy stat, we can invest 60 Training Points into Energy to reach 250 Total Points which enables Barblast to use 3 Rejuvenates, 3 Water Bombs and 1 Toxiblast. 250 is a great benchmark energy stats so this strategy can be applied to a plethora of Loomians. `,
                        `As for the remaining 40 TPs, we can insert them into the Ranged Defense stat in order to increase our bulk on the less defensive side; investing into the Ranged Attack stat is also an option.`,
                        require("@/assets/images/setMaking/DefensiveSet1/TrainingPoints.png"),
                        `Theres a few options to our disposal when it comes to what Barblast will be holding. Energy Orb is very helpful to conserve energy and reduce the energy cost of your moves, especially since Barblast will click Rejuvenate, a move with 72 energy cost, quite often. If preserving energy isnt a worry for you, Heavy Armour helps strengthen Barblasts Melee Defense. This will hinder your energy stat leading to only 3 Rejuvenates being available at 250 energy so keep this in mind.`,
                        `Relating to the type effectiveness section, Barblast can find itself in tricky matchups while being able to hit the opponent super effectively i.e. Ikazune, Harvesect and Cephalops. In these situations, the respective pearl - which halves the strength of a move of the corresponding type - can enable Barblast to sustain itself and win in a 1v1. Be cautious, since pearls are a one-use item.`,
                    ]},
                    {
                    Name: "Defensive Set - Example 2",
                    Description: [
                        `Despite appearing right at the beginning of the game, Gastroak was considered as an absolute failure and was largely forgotten by the community; however, with the introduction of Soul Burst, it shot up in usage due to its elite utility as a defensive Loomian.`,
                        `Terraform-Gastroaks base stats are a breathtaking upgrade from its normal form. A 140 Health stat, along with a 140 Ranged Defence stat, allows it to sponge many hits. It has a respectable Ranged Attack stat of 105 and an adequate energy stat of 88. On the other hand, Terraform-Gastroaks Melee side is quite middling with a 60 Melee Attack stat and 45 Melee Defence stat but this is fine, due to Gastroak not needing to rely on them. As for its 65 Speed stat, it's a fair compromise given its monstrous stats. `,
                        require("@/assets/images/setMaking/DefensiveSet2/BaseStats.png"),
                        `Generally, Plant isnt seen as a good typing due to its interactions but, along with its earth typing gained from Soul Burst, Terraform-Gastorak does boast 2 immunities (light and electric) and an earth resistance. Conversely, Terraform-Gastroak has a 2x weakness to Fire, Dark and Bug and a quad weakness to Ice (Though this will be further touched upon in the Ability section).`,
                        require("@/assets/images/setMaking/DefensiveSet2/TypeEffectiveness.png"),
                        `Given its incredibly bulky nature by having a high Health and Ranged Defense stat, Terraform-Gastroak can use Very Clever Frail Sluggish. Terraform-Gastroak also has the utility to support this type of set. The negative personalities are chosen because theyre low and therefore wont be used much.`,
                        `Terraform-Gastroak has access to only one ability: Insulated which not only turns its quad weakness to ice into a normal weakness but also makes Fire type attacks neutral. Despite this, it's recommended to run Ungracious Host as a normal Gastroak to deter any health-draining move.`,
                        require("@/assets/images/setMaking/DefensiveSet2/Ability.png"),
                        `Stratagem is a no brainer with a Ranged Defense stat of 140, allowing it to exert offensive pressure whilst remaining defensive. Tamp, which increases its Melee Defence and Ranged Defence, is also helpful in making Gastroak deal more damage with stratagem whilst also improving its Defence stats`,
                        `Poison is also a very potent move for asserting pressure onto the opponent especially if they do try to swap into a resist if theyre expecting you to use Stratagem.`,
                        `Lastly, Photosynthesis is a must-have on Gastroak just like how any other defensive Loomians likes reliable recovery. As a bonus, it has an animation allowing you to animation stall in front of your opponent.`,
                        require("@/assets/images/setMaking/DefensiveSet2/Moveset.png"),
                        `200 Health and 200 Ranged Defence makes Gastroak as tanky as possible allowing it to not only take as many hits as possible but also increase its damage output with Stratagem.`,
                        `20 Energy can be used for 5 Stratagems and 2 Tamps (or Poisons).`,
                        `56 Speed enables it to outspeed Ursoul and 24 Melee Defence ensures a Blaze Chomp from Sedimars is a 4 hit KO - which would hit Gastroak for neutral effectiveness.`,
                        require("@/assets/images/setMaking/DefensiveSet2/TrainingPoints.png"),
                        `Of course, since this is a Soul Burst Loomian, it will be running its respective Burst Crystal: Gastroburst. This can be seen as disadvantageous since any other defensive loomian could hold a heavy shield/armour or mysterious dust however there really isnt an alternative.`,
                        require("@/assets/images/setMaking/DefensiveSet2/Gastroburst.png"),
                    ]}
                ]
            }
        },
        methods: {
            SwitchTabs(Name) {
                var i, tabcontent
                tabcontent = document.getElementsByClassName("description")

                // For all the description boxes, make them all invisible
                for (i = 0; i < tabcontent.length; i++) {
                    tabcontent[i].style.display = "none";
                }

                // Now get the element that the user selected and make that visible
                document.getElementById(Name).style.display = "block";
            },
            isImagePath(str) {
                return /\.(jpg|jpeg|png|gif|webp)$/i.test(str);
            }
        }
    }
</script>
<style scoped>
    .box {
        margin-top: 20px;
        background-color: #0057b4;
        border-radius: 25px;
        min-width: 280px;
    }

    .selection {
        padding: 10px;
        background-color: azure;
        border-radius: 25px 25px 0px 0px;
    }

    .selection button {
        margin: 5px 10px;
    }

    .description {
        display: none;
        padding: 5px 2%;
    }

    .image {
        display: grid;
        margin: auto;
        max-width: 800px;
        width: 60%;
        min-width: 260px;
        border-radius: 10px;
    }
</style>